body{
  margin: 0;
}

.navBar{
    width: 88%;
    padding: 0 6%;
    height: 13vh;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.navBar-brand{
    display: flex;
}

.svnit-logo{
    /* display: none; */
    height: 7vh;
    width: auto;
}

a.drishti-contain{
    padding-top: 0.5vh;
    padding-left: 14px;
}

.drishti-logo{
    height: 6.5vh;
    width: auto;
}

.navBar-menu{
    display: flex;
    justify-content: center;
    align-items: center;
}

.navBar-menu a{
    text-decoration: none;
    font-size: 1em;
    font-weight: 600;
    color: #f0f0ff;
    border-bottom: 0.16em solid transparent;
    font-family: 'Lato', 'Open Sans', sans-serif;
    padding: 0.25em 10px;
    margin-left: 35px;
    text-transform: uppercase;
    transition: 0.3s ease;
}

.navBar-menu a:hover{
    border-bottom: 0.16em solid #ffec44;
    color: #ffec44;
    transition: all 0.25s ease;
}
.first-div{
    height: 100vh;
    width: 100%;
    background-image: url(/drishti-website-revamped/static/media/section_1_back_landscape.175d6286.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.container{
    width: 100%;
    height: 87vh;
    display: grid;
    grid-template-columns: 1fr 1fr;
}


/* Left Section */


.left-sec{
    width:100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}
.robotic{
    position: relative;
    max-height: 100%;
    min-height: 42vw;
}
.robotic img.robo-hand{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 94%;
    z-index: 50;
}
.robotic img.index-circle{
    position: absolute;
    left: 78.5%;
    bottom: 82%;
    width: 18%;
    z-index: 60;
}
.robotic img.quadrilateral{
    position: absolute;
    left: 16%;
    bottom: 56%;
    width: 38%;
    z-index: 40;
}
.robotic img.thumb-circle{
    position: absolute;
    left: 51%;
    bottom: 22%;
    width: 25%;
    z-index: 40;
}
.robotic img.striped{
    position: absolute;
    left: 10%;
    bottom: 42%;
    width: 23%;
    z-index: 40;
}


/* Right Section */


.right-sec{
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.title{
    margin-left: 4em;
}
.title h1{
    font-family: 'Anonymous Pro', monospace;
    font-weight: 700;
    color: #f8fbfd;
    font-size: 8em;
    text-transform: uppercase;
    margin: 0;
}
.title h2{
    font-family: 'Josefin Sans', sans-serif;
    font-weight: 400;
    color: #f8fbfd;
    font-size: 2em;
    margin: 0.3em 0;
}
.title a.explore{
    text-decoration: none;
    color: #f8fbfd;
    background-color: #dc3c4d;
    font-size: 1.1em;
    font-weight: 600;
    font-family: 'Open Sans', sans-serif;
    padding: 10px 24px 10px 34px;
    border-radius: 1.4em;
} 
.title a.notices{
    margin-top: 20px;
    text-decoration: none;
    color: #dc3c4d;
    background-color: #f8fbfd;
    font-size: 1.1em;
    font-weight: 600;
    font-family: 'Open Sans', sans-serif;
    padding: 10px 5px 10px 34px;
    border-radius: 1.4em;
} 
.title a.notices .imp{
    font-size: 2em;
    margin-bottom: -0.18em;
}





@media only screen and (max-width: 768px) {
    .first-div{
        background-image: url(/drishti-website-revamped/static/media/section_1_back_portrait.498a01e0.jpg);
    }  
}

